import { Component } from '@angular/core';
import { Resume, Experience, Education, Skill } from './resume';
import { ScriptService } from './script.service';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

declare let pdfMake: any ;
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  resume = new Resume();

  marked = false;
  currentCompany = false;
  
  toggleVisibility(e){
   this.marked= e.target.checked;
 }
  degrees = ['SSC', 'HSC', 'B.E.', 'M.E.', 'B.Com', 'M.Com'];

  constructor(private scriptService: ScriptService) {
    this.resume = JSON.parse(sessionStorage.getItem('resume')) || new Resume();
    if (!this.resume.experiences || this.resume.experiences.length === 0) {
      this.resume.experiences = [];
      this.resume.experiences.push(new Experience());
    }
    if (!this.resume.educations || this.resume.educations.length === 0) {
      this.resume.educations = [];
      this.resume.educations.push(new Education());
    }
    if (!this.resume.skills || this.resume.skills.length === 0) {
      this.resume.skills = [];
      this.resume.skills.push(new Skill());
    }

    console.log('Loading External Scripts');
    this.scriptService.load('pdfMake', 'vfsFonts');
  }

  addExperience() {
    this.resume.experiences.push(new Experience());
  }

  addEducation() {
    this.resume.educations.push(new Education());
  }

  generatePdf(action = 'open') {
    console.log(pdfMake);
    const documentDefinition = this.getDocumentDefinition();

    switch (action) {
      case 'open': pdfMake.createPdf(documentDefinition).open(); break;
      case 'print': pdfMake.createPdf(documentDefinition).print(); break;
      case 'download': pdfMake.createPdf(documentDefinition).download(); break;

      default: pdfMake.createPdf(documentDefinition).open(); break;
    }

  }


  resetForm() {
    this.resume = new Resume();
  }

  getDocumentDefinition() {
    sessionStorage.setItem('resume', JSON.stringify(this.resume));
    return {
      content: [
        {
          columns: [
            [{
              text: this.resume.name,
                  bold: true,
                  fontSize: 28,
                  alignment: 'left',
                  margin: [0, 0, 0, 5],
                  borderBottom:10,
                  color:'#d83f5e'
            },
            // {
            //   text: this.resume.currentProfile,
            //   bold: false,
            //   fontSize: 12,
            //   alignment: 'left',
            //   borderBottom:10,
            //   color:'#333333'
            // },
            {
              text: this.resume.email,
              fontSize: 10,
              alignment: 'left',
              margin: [0, 0, 0, 2],
              color:'#000000',
              lineHeight:1.3,
            },
            {
              text: this.resume.contactNo,
              fontSize: 10,
              alignment: 'left',
              margin: [0, 0, 0, 0],
              color:'#000000',
              lineHeight:1.3,
            },
            {
              text: this.resume.socialProfile,
              link: this.resume.socialProfile,
              bold: false,
              fontSize: 10,
              alignment: 'left',
              margin: [0, 0, 0, 5],
              borderBottom:10,
              color:'blue'
            },
            ],
            [
              this.getProfilePicObject(),
            ]
          ]
        },
      //   Experience Starts
        {
			columns: [
				{
					text: 'Experience',
					width: 125,
               bold: false,
               fontSize: 18,
               alignment: 'left',
               margin: [0, 15, 0, 2],
               color:'#818084',
				},
				[
               //   'Experience',         
                this.getExperienceObject(this.resume.experiences),
				],
			]
		},
      //   Skills Starts
        {
			columns: [
				{
					text: 'Skills  ',
					width: 125,
               bold: false,
               fontSize: 18,
               alignment: 'left',
               margin: [0, 25, 0, 2],
               color:'#818084',
            },
            {
               margin: [0, 25, 0, 6],
               type: 'none',
               fontSize: 10,
               alignment: 'left',
               color:'#000000',
               lineHeight:1.3,
               padding:0,
               ul : [
                 ...this.resume.skills.filter((value, index) => index % 3 === 0).map(s => s.value),
               ]
             },
             {
               margin: [0, 25, 0, 6],
               type: 'none',
               fontSize: 10,
               alignment: 'left',
               color:'#000000',
               lineHeight:1.3,
               padding:0,
               ul : [
                 ...this.resume.skills.filter((value, index) => index % 3 === 1).map(s => s.value)
               ]
             },
             {
               margin: [0, 25, 0, 6],
               type: 'none',
               fontSize: 10,
               alignment: 'left',
               color:'#000000',
               lineHeight:1.3,
               padding:0,
               ul : [
                 ...this.resume.skills.filter((value, index) => index % 3 === 2).map(s => s.value)
               ]
             }
				
			]
		},
      //   Education Starts
      {
			columns: [
				{
					text: 'Education  ',
					width: 125,
               bold: false,
               fontSize: 18,
               alignment: 'left',
               margin: [0, 25, 0, 2],
               color:'#818084',
            },
            this.getEducationObject(this.resume.educations),
			]
		},





      


      //   {
      //     text: 'Other Details',
      //     style: 'header'
      //   },
      //   {
      //     text: this.resume.otherDetails
      //   },
         
        {
          text: 'Signature',
          style: 'sign'
        },
        {
          columns : [
            //   { qr: this.resume.name + ', Contact No : ' + this.resume.contactNo, fit : 100 },
              {
              text: this.resume.name,
              alignment: 'right',
              fontSize: 14,
              color:'#d83f5e',
              lineHeight:1.3,
              }
          ]
        }
      ],
      info: {
        title: this.resume.name + '_RESUME',
        author: this.resume.name,
        subject: 'RESUME',
        keywords: 'RESUME, ONLINE RESUME',
      },
        styles: {
          header: {
            fontSize: 18,
            bold: true,
            margin: [0, 20, 0, 10],
            decoration: 'underline'
          },
          name: {
            fontSize: 16,
            bold: true
          },
          jobTitle: {
            fontSize: 14,
            bold: true,
            italics: true
          },
          sign: {
            margin: [0, 50, 0, 10],
            alignment: 'right',
            italics: true
          },
          tableHeader: {
            bold: true,
          }
        }
    };
  }

  getExperienceObject(experiences: Experience[]) {

    const exs = [];

    experiences.forEach(experience => {
      exs.push(
        [{
          columns: [
            [{
               text: experience.employer,
               fontSize: 14,
               alignment: 'left',
               margin: [0, 15, 0, 0],
               color:'#d83f5e',
               lineHeight:1.3,
            },
            {
              text: experience.jobTitle,
              fontSize: 10,
              alignment: 'left',
              margin: [0, 0, 0, 0],
              color:'#000000',
              lineHeight:1.3,
            },
            {
               text:  experience.exStartDate + ' To ' + experience.exEndDate,
               fontSize: 10,
               alignment: 'left',
               margin: [0, 0, 0, 2],
               color:'#000000',
               lineHeight:1.3,
            },
            {
              text: experience.jobDescription,
              fontSize: 10,
              alignment: 'left',
              margin: [0, 0, 0, 5],
              color:'#999999',
              lineHeight:1.3,
            }]
          ]
        }]
      );
    });

    return {
      table: {
        widths: ['*'],
        body: [
          ...exs
        ]
      },
      layout: 'noBorders'
    };
  }

  getEducationObject(educations: Education[]) {
    return {
      margin: [0, 25, 0, 2],
      table: {
        widths: ['*', '*', '*', '*'],
        body: [
          [{
            text: 'Degree',
            style: 'tableHeader'
          },
          {
            text: 'College',
            style: 'tableHeader'
          },
          {
            text: 'Passing Year',
            style: 'tableHeader'
          },
          {
            text: 'Result',
            style: 'tableHeader'
          },
          ],
          ...educations.map(ed => {
            return [ed.degree, ed.college, ed.passingYear, ed.percentage];
            
          })
        ]
      },
      layout: {
         fillColor: function (rowIndex, node, columnIndex) {
            return (rowIndex === 0) ? '#CCCCCC' : null;
         }
      },
      fontSize: 10,
      alignment: 'left',
      color:'#000000',
      lineHeight:1.3,
    };
  }

  getProfilePicObject() {
    if (this.resume.profilePic) {
      return {
        image: this.resume.profilePic ,
        width: 75,
        alignment : 'right'
      };
    }
    return null;
  }

  fileChanged(e) {
    const file = e.target.files[0];
    this.getBase64(file);
  }

  getBase64(file) {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      console.log(reader.result);
      this.resume.profilePic = reader.result as string;
    };
    reader.onerror = (error) => {
      console.log('Error: ', error);
    };
  }

  addSkill() {
    this.resume.skills.push(new Skill());
  } 
}
